.jumbotron {
    background: #A3BCF9;
    box-shadow: 0 6px 12px rgba(0,0,0,0.16), 0 6px 12px rgba(0,0,0,0.23);
    text-shadow: 1px 1px 1px #000;
    padding: 1em;
}

.title {
    color: #D1D2F9 !important;
    font-family: 'Roboto', sans-serif;
    font-size: 40px;
    font-weight: bolder;
}

.description {
    color: #fff !important;
    font-size: 1.5em;
    font-weight: 500;
    letter-spacing: .1em;
}

.Navbar {
    background-color: 	#576490;
    line-height: 15px;
    text-shadow: 3px 3px 6px #000;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 2147483647;
}

.response {
    color: white !important;
    font-family: 'Roboto', sans-serif;
    font-size: 25px;
}

.brand {
    color: #D1D2F9 !important;
    font-family: 'Roboto', sans-serif;
    text-transform: uppercase;
    letter-spacing: .25em;
    font-size: 25px;
}

.score {
    font-family: 'Roboto', sans-serif;
    font-size: 22px;
    text-transform: uppercase;
    letter-spacing: .25em;
}

.number {
    color: #fff !important;
    font-size: 22px;
}
body {
  background-color: #7796CB;
  padding-bottom: 50px;
}

.card {
  border: 1px solid #576490;
  background-color: #D1D2F9;
  height: 225px;
  margin: 10px;
}

.card:hover {
  box-shadow: 0 6px 12px #272D42, 0 6px 12px rgba(0,0,0,0.23);
}

.card:active {
  box-shadow: 0px 0px 0px 5px #576490; 
}

.cardImage {
  width: 160px;
  height: 220px;
  padding-top: 5px;
  pointer-events: none;
}
